	export default{
		data() {
			return {
				h:'0',
				s:'0',
				m:'0',
				list:[{
					title:'微信支付',
					describe:'微信安全支付',
					val:1,
					icon:'icon-weixintubiao'
				},{
					title:'支付宝支付',
					describe:'微信安全支付',
					val:0,
					icon:'icon-zhifubao'
				},{
					title:'现金支付',
					describe:'现金到店支付',
					val:0,
					icon:'icon-zhanghuyue'
				}]
			}
		},
		components:{
		},
		onLoad(e) { //获取数的
			
		},	 
        created() {
		  this.countTime();//倒计时
		},
		methods:{
			countTime(){
                //获取当前时间
                var date = new Date();
                var now = date.getTime();
                //设置截止时间
                var endDate = new Date("2020-10-22 23:23:23");
                var end = endDate.getTime();
                //时间差
                var leftTime = end - now;
                //定义变量 d,h,m,s保存倒计时的时间
                if (leftTime >= 0) {
                let  d = Math.floor(leftTime / 1000 / 60 / 60 / 24);
                    this.h = Math.floor(leftTime / 1000 / 60 / 60 % 24);
					this.h=this.h>10?this.h:'0'+this.h
                    this.m = Math.floor(leftTime / 1000 / 60 % 60);
					this.m=this.m>10?this.m:'0'+this.m
                    this.s = Math.floor(leftTime / 1000 % 60);
					this.s=this.s>10?this.s:'0'+this.s
                }
                console.log(this.s);
                //递归每秒调用countTime方法，显示动态时间效果
                setTimeout(this.countTime, 1000);
            },
			tabClick(index){
				for(let i=0;i<this.list.length;i++){
					this.list[i].val=0
				}
				this.list[index].val=1;
			},
			bntClick(){
				uni.navigateTo({
					url:"../pay/paymentStatus"
				})
			}
		}	
	}
